import React from 'react'
import { observer, inject } from 'mobx-react'
import '../styles/Main.css'

@inject('userStore')
@observer
class Main extends React.Component{
  render(){
    const { userStore } = this.props
    const { usermail } = userStore
    return (<div>
      <header>
        <h1>BEST VIDEO</h1>
        <div className="bottom">
          <h3>{usermail}</h3>
          <p>
            "좋아하는 유튜브 비디오를 모아두고 싶었다.
            이 페이지는 내가 좋아하는 유튜브 비디오를 수집한 페이지다.
            내 취향을 있는 그대로 노출한다는 게 약간 마음에 걸리긴 하지만,
            사실 이 페이지에는 나 외에 다른 사람이 들어 올 일이 거의 없다."
          </p>
          <nav>
            <ul>
              <li>유튜브</li>
              <li>페이스북</li>
              <li>인스타그램</li>
              <li>트위터</li>
              <li>이메일</li>
            </ul>
          </nav>
        </div>
      </header>

      <main>
        <section className="movie">
          <div className="wrapper">
            <div className="movie__header">
              <h4>게임 어몽어스 캐릭터 만들기</h4>
              <h5>달려라 치킨 Dalchi</h5>
            </div>
            <div className="movie__video">
              <iframe width="560" height="315" 
                src="https://www.youtube.com/embed/dmsDZxjUXxI?controls=0" 
                frameborder="0" allow="accelerometer; autoplay; clipboard-write; 
                encrypted-media; gyroscope; picture-in-picture" allowfullscreen>
              </iframe>
            </div>
          </div>
        </section>
        <section className="movie">
          <div className="wrapper">
            <div className="movie__header">
              <h4>게임 폴가이즈 캐릭터 만들기</h4>
              <h5>달려라 치킨 Dalchi</h5>
            </div>
            <div className="movie__video">
              <iframe width="560" height="315" 
                src="https://www.youtube.com/embed/_1RkZ3IGYAg?controls=0" 
                frameborder="0" allow="accelerometer; autoplay; clipboard-write; 
                encrypted-media; gyroscope; picture-in-picture" allowfullscreen>
              </iframe>
            </div>
          </div>
        </section>
        <section className="movie">
          <div className="wrapper">
            <div className="movie__header">
              <h4>네모바지 스폰지밥 집 만들기</h4>
              <h5>달려라 치킨 Dalchi</h5>
            </div>
            <div className="movie__video">
              <iframe width="560" height="315" 
                src="https://www.youtube.com/embed/ZP0vjqcvwSQ?controls=0" 
                frameborder="0" allow="accelerometer; autoplay; clipboard-write; 
                encrypted-media; gyroscope; picture-in-picture" allowfullscreen>
              </iframe>
            </div>
          </div>
        </section>

        <footer>
          <p>
            <strong>BEST VIDEO</strong> BY <strong>Noh Ga-yeon</strong>
          </p>
        </footer>
      </main>
    </div>)
  }
}

export default Main